import { FileAddOutlined, FileDoneOutlined, FileExcelOutlined, FileSyncOutlined } from '@ant-design/icons'
import { Card, Col, Row, Skeleton } from 'antd'
import moment from 'moment';
import React, { useEffect, useState } from 'react'

function ActionCard({actionType,username, date, actionDescription,loading, timeout}) {
    const [stateShow, setStateShow] = useState(true)
    useEffect(() => {
        setTimeout(() => {
            setStateShow(false)
        }, timeout);
      }, []);

    return stateShow && <Card style={{ width: '100%', marginTop: 16 }}>
            <Skeleton loading={loading} active>           
                <Row>
                    <Col xs={24} md={3} style={{textAlign:'center', padding:10, borderRadius:20}}>
                        {actionType === 'UPDATE' && <FileDoneOutlined style={{fontSize:120, color:'#faad14'}} />}
                        {actionType === 'DELETE' &&<FileExcelOutlined style={{fontSize:120, color:'#f5222d'}} />}
                        {actionType === 'CREATE' &&<FileAddOutlined style={{fontSize:120, color:'#52c41a'}} />}
                        {actionType === 'READ' &&<FileSyncOutlined  style={{fontSize:120, color:'#13c2c2'}} />}
                    </Col>
                    <Col xs={24} md={21} style={{padding:10}}>
                        <h2><b>{username}</b></h2>
                        <p style={{color:'#bfbfbf', fontStyle:'italic'}}>{moment(new Date(date)).format("ddd DD-MMM-yyyy hh:mm")}</p>
                        <p style={{fontSize:18}}>{actionDescription}</p>
                    </Col>
                </Row>
            </Skeleton>
        </Card>
    
}

export default ActionCard
