import { Layout, Menu, Image,Divider, Popconfirm, Modal } from 'antd';
import {
    SettingOutlined,
    ContainerOutlined,
    TeamOutlined,
    ExportOutlined,
    HomeOutlined,
    LoginOutlined,
    UserOutlined,
    BankOutlined,
    ExperimentOutlined,
    ReconciliationOutlined,
    AuditOutlined,
    BarChartOutlined
} from '@ant-design/icons';

import {IoSchoolOutline} from 'react-icons/io5'

import {GET_ROLES, PROFILE} from '../graphql/auth'
import { useQuery, useSubscription } from '@apollo/client';
import Logo from '../asset/srLogo.png'
import { Link } from 'react-router-dom'
import { useContext, useEffect, useState } from 'react';
import { HeaderData } from '../context/headerContext';
// import { auth } from '../api/firebase';
import { currentUserVar,rolesVar } from '../cache';
import { keyMenu } from '../function/fn'
// import AuthRoute from '../component/auth/AuthRoute'
import { useApolloClient } from '@apollo/client';
import { useHistory } from 'react-router';

import { openNotification } from '../static/own-comp';
import { SUB_GET_ACTION_USER } from '../graphql/action';
import { AiFillShop } from 'react-icons/ai';
import { FaPeopleCarry } from 'react-icons/fa';

const { Sider } = Layout;
const { SubMenu } = Menu;
const { confirm } = Modal;



export default function MenuHeader() {
    const client  = useApolloClient();
    const history = useHistory()
    const { urlPath, user } = useContext(HeaderData)
    const [collapsed, setCollapsed] = useState(false)

    const [actionData, setActionData] = useState({})

    const { data: profile } = useQuery(PROFILE);
    
    const page = profile?.currentUser?.pages?.map(e => e.page)

    const { data: {roles} } = useQuery(GET_ROLES);
    const handleLoggout = async()=>{
        localStorage.removeItem("token")
        localStorage.clear()
        currentUserVar(false)
        //  await client.resetStore();
        // history.push("/")
    }

    const { data:notify,loading } = useSubscription(SUB_GET_ACTION_USER);
 
    useEffect(()=>{
    if(notify!==undefined){
        let data = notify.userActionWithPersonalInfo;
        openNotification({username:data?.username,action:data?.userAction})
        setActionData(data)
        console.log(notify)
    }
    },[notify])

    console.log(actionData)

    function showConfirm() {
        confirm({
            title: '?',
            okType: 'danger',
            okText: "",
            cancelText: "",
            onOk() {
                handleLoggout();
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }

    const authRoute = (page=[],desc,title,icon) => {

        const auth = page.includes(desc);
        return auth && <Menu.Item key={`${desc}`} style={{fontSize:16}} icon={icon}>  <Link to={desc} /> {title}</Menu.Item>
      
            // <Menu.Item key={`${desc}`} style={{fontSize:16}}> {title} fhello <Link to={desc} /> </Menu.Item>}
     
    }
    
   
    return (
        <Sider collapsible collapsed={collapsed} onCollapse={() => setCollapsed(!collapsed)}
        className="side-custom"
            style={{
                // backgroundColor: '#06385f' 
                // backgroundColor: '#22252A' 
                // backgroundColor:'#1e3b87'
                // backgroundColor:'#005438'
                // background: 'repeating-linear-gradient(-55deg,#005b3d,#005b3d 30px,#005438 30px,#005438 40px)'
                // background: 'url("../asset/covid-19-bg.png") repeat center'

            }}>
            <Link to="/">
                <div className="logo">
                    <Image width={"100%"} className="img-logo" src={Logo} preview={false} />
                </div>
            </Link>

            {/* {
                openNotification({username:"test",action:"test"})
            } */}

            <Divider style={{background:'#FFF',height:2}} />
            <Menu theme="dark"
                // defaultSelectedKeys={[urlPath]} 
                selectedKeys={[keyMenu(urlPath)]}
                mode="inline"
                style={{
            
                }}>

                    {authRoute(page,"/","",<HomeOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/sample","",<ExperimentOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/interview"," Covid-19",<AuditOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/hospital","",<ReconciliationOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/school","",<IoSchoolOutline style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/quarantine","",<BankOutlined style={{fontSize:20}}  /> ) }
                { page?.includes("/shop") || page?.includes("/seller") ?
                <SubMenu key="sub2" style={{fontSize:16}} icon={<ContainerOutlined style={{fontSize:20,color:'#DDDD'}}  />} title={<span style={{color:'#DDDD'}}>QR Code</span>}>
                    {authRoute(page,"/shop","",<AiFillShop style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/seller","",<FaPeopleCarry style={{fontSize:20}}  /> ) }
                </SubMenu>
                :null}
                    {authRoute(page,"/people","",<TeamOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/graph","",<BarChartOutlined style={{fontSize:20}}  /> ) }

                { page?.includes("/reportDaily") || page?.includes("/reportExcel") ?
                <SubMenu key="sub1" style={{fontSize:16}} icon={<ContainerOutlined style={{fontSize:20,color:'#DDDD'}}  />} title={<span style={{color:'#DDDD'}}></span>}>
                    {authRoute(page,"/reportDaily","",<TeamOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/reportExcel"," EXCEL",<TeamOutlined style={{fontSize:20}}  /> ) }
                </SubMenu>
                :null}

                    {authRoute(page,"/case","",<LoginOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/user","",<UserOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(page,"/finding","",<TeamOutlined style={{fontSize:20}}  /> ) }
                    <Menu.Item
                        // onClick={() => auth.signOut()}
                        style={{fontSize:16}} 
                        key="6"
                        icon={<ExportOutlined style={{fontSize:20}}  />}
                        onClick={showConfirm}
                        // onClick={() => handleLoggout()}
                    >
                        
                    </Menu.Item>
            </Menu>
        </Sider>
    )
}