import { Space, Popconfirm,Tooltip } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined,
    QrcodeOutlined
} from '@ant-design/icons';
import { AiOutlineShop } from 'react-icons/ai'
import {GiShop} from 'react-icons/gi'
import { Link } from 'react-router-dom';

export const sellerCol = ({handleDelete,limit,page,handleGenerateQR,handleOpenAddShop}) => {
    let no = 0 + (page * limit) - limit
    var array = [
        {
            title: '.',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            // render: (text, record, i) => (
            //     <span>
            //         {no+= 1}
            //     </span>
            // ),
        },
        {
            title: '',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 130,
        },
        
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 130,
            render: (text, record) => (
                <span size="middle">
                    {
                        record.lastName+" "+record.firstName
                    }
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'nationality',
            key: 'nationality',
            width: 120,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 250,
            render: (text, record) => (
                <span>
                    {(record.village !== "") && record.village+", "}
                    {record.commune !== "" && record.commune+", "}
                    {record.district !== "" && record.district+", "}
                    {record.province && record.province}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'occupation',
            key: 'occupation',
            width: 120,
        },
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 100,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                    {/* <Tooltip
                        title=" QR Code"
                    >
                        <span className="link" ><QrcodeOutlined onClick={()=> handleGenerateQR(record)} /></span>
                    </Tooltip>

                    <Tooltip
                        title=""
                    >
                        
                        <span className="link" ><AiOutlineShop style={{fontSize:18}} onClick={()=> handleOpenAddShop(record)} /></span>
                    </Tooltip> */}

                </Space>
            ),
        }
    ]
    return array
}