import React, { useContext, useState, useEffect } from 'react'
import { Row, Col, Button, Input, Table, message, Select, DatePicker,Form,Checkbox } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { PeopleController } from '../context/peopleContext';
import { interviewCol } from '../component/interview/tableColumn/interviewColumn';
import AddInterview from '../component/interview/modal/addInterview';
import EditInterview from '../component/interview/modal/editInterview';
import { useQuery, useMutation } from '@apollo/client';
import { DELETE_PERSONALINFO_BY_ID, GET_INTERVIEW } from '../graphql/people';
import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import { ListSelect } from '../static/own-comp'
import {
    districForFilter,communeData, villageData
} from "../context/headerContext";
import {
    convertToCommune,
    convertToDistrict,
    convertToVillage,
} from "../function/fn";
import ExportToExcel from "../component/export/exportToExcel";
import {PeopleList} from '../asset/exportExcel/reportExcel'
import moment from 'moment'

const {Option} = Select
const { RangePicker } = DatePicker;

export default function Interview() {

    let [form] = Form.useForm()

    const [interviewType, setInterviewType] = useState(false)

    const [openAdd, setOpenAdd] = useState(false)
    const [openEdit, setOpenEdit] = useState(false)

    const [editData, setEditData] = useState({})

    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    const [interviewedData, setInterviewedData] = useState([])
    const [district, setDistrict] = useState("");
    const [filterCommune,setFilterCommune] = useState("")
    const [filterVillage,setFilterVillage] = useState("")

    const [confirm, setConfirm] = useState(true);
    const [recovered, setRecovered] = useState(false);
    const [death, setDeath] = useState(false);

    // interview:$interview, 
    // page:$page,
    // limit:$limit,
    // keyword:$keyword,
    // startDate:$startDate,
    // endDate:$endDate,
    // district:$district,
    // village:$village,
    // commune:$commune,
    // recovered:$recovered,
    // death:$death,
    // isFillDate:$isFillDate

    const [filterData,setFilterData] = useState({
        village:null,
        commune:null,
        district:null,
        death:false,
        recovered:false,
        keyword:"",
        limit:15,
        interview:null,
        startDate:null,
        endDate:null,
        isFillDate:false,
    })

    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const [filterDistrict, setFilterDistrict] = useState("")

    const { data, loading, error, refetch } = useQuery(GET_INTERVIEW, {
        variables: {
            interview: filterData.interview,
            page: page,
            limit: filterData.limit,
            keyword: filterData.keyword,
            startDate: filterData.startDate,
            endDate:filterData.endDate,
            district:filterData.district,
            commune: filterData.commune,
            village:filterData.village,
            recovered:filterData.recovered,
            death:filterData.death,
            isFillDate:filterData.isFillDate
        },
        onCompleted: ({ getConfirmedPersonalInfoByInterviewWithPagination }) => {
            // console.log(getConfirmedPersonalInfoByInterviewWithPagination)
            setInterviewedData(getConfirmedPersonalInfoByInterviewWithPagination)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only"
    })
    // const getPersonalInfoWithPagination 
    useEffect(() => {
        if (data) {
            setInterviewedData(data?.getConfirmedPersonalInfoByInterviewWithPagination)
        }
    }, [data])

    useEffect(() => {
        refetch()
    }, [page, limit, keyword, filterDistrict])
      
    const onFinish = (values) => {
        let startDate = null
        let endDate = null
        if(values.filterDate!==undefined){
            startDate = new Date(values.filterDate[0])
            endDate = new Date(values.filterDate[1])
        }
    
        // village:null,
        // commune:null,
        // district:null,
        // death:false,
        // recovered:false,
        // keyword:"",
        // limit:15,
        // interview:false,
        // startDate:null,
        // endDate:null,
        // isFillDate:false,

        console.log({
            ...values,
            keyword:values.keyword!==undefined? values.keyword:"",
            district:values.district!==undefined ? values.district:null,
            commune:values.commune!==undefined ? values.commune:null,
            village:values.village!==undefined ? values.village:null,
            recovered: values.recovered ? true : false,
            death: values.death ? true : false,
            startDate: values.filterDate!== undefined ? startDate:null,
            endDate:values.filterDate!==undefined ? endDate:null,
            isFillDate:values.isFillDate? values.isFillDate:false,
            interview: values.interview ? true : false,
            limit:values.limit!==undefined ? limit:15
        })
        setFilterData({
            ...values,
            keyword:values.keyword!==undefined? values.keyword:"",
            district:values.district!==undefined && values.district!=="----" ? values.district:null,
            commune:values.commune!==undefined && values.commune!=="----" ? values.commune:null,
            village:values.village!==undefined && values.village!=="----" ? values.village:null,
            recovered: values.recovered ? true : false,
            death: values.death ? true : false,
            startDate: values.filterDate!== undefined ? startDate:null,
            endDate:values.filterDate!==undefined ? endDate:null,
            isFillDate:values.isFillDate? values.isFillDate:false,
            interview: values.interview!==undefined ? values.interview : null,
            limit:values.limit!==undefined ? limit:15
        })


    };

    const showAll =()=>{
        form.resetFields()
        form.submit()
        // setFilterData({
        //     ...filterData,
        //     keyword:"",
        //     district:null,
        //     commune:null,
        //     village:null,
        //     recovered:false,
        //     death: false,
        //     startDate:null,
        //     endDate:null,
        //     isFillDate:false,
        //     interview:null,
        //     // limit:values.limit!==undefined ? limit:15
        // })
        refetch()
    }

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setFilterCommune(e ==="----" ? "":e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
        setFilterVillage(e === "----" ? "":e)
    };

    const [deletePersonalInfo, { loading: deleteLoading }] = useMutation(DELETE_PERSONALINFO_BY_ID, {
        onCompleted: () => {
            message.success("")
        }
    })

    const handleDelete = (e) => {
        deletePersonalInfo({
            variables: {
                id: e
            }
        })
    }

    const handleEdit = (e) => {
        setEditData(e)
        setOpenEdit(true)
    }

    const handleCheckConfirm=(e)=>{
        
        setConfirm(e)
        if(!e){
            form.setFieldsValue({
                confirmedAt:null,
                covidType:null 
            })
        }
    }

    const handleCheckRecover=(e)=>{
        
        setRecovered(e)
        if(e){
            form.setFieldsValue({
                death:false
            })
        }
    }

    const handleCheckDeath=(e)=>{
        setDeath(e)
        if(e){
            form.setFieldsValue({
                recovered:false
            })
        }
    }

    const onSelectDate = (e) => {
        
        if(e !== null){
            let startDate = new Date(e[0])
            let endDate = new Date(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
            // console.log(endDate)
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const setToDistrictFn = (e) => {
        setFilterDistrict(e === "----" ? "" : e)
        if(e!==""){
            setFilterCommune("")
            setFilterVillage("")
            form.setFieldsValue({
                commune: null,
                village: null,
            });
        }
        form.setFieldsValue({
            district:e,
            commune: null,
            village: null,
        });
    };

    const splitDot = (e)=>{
        let newText = e;
        if(newText!=="" || newText!== undefined){
            // newText="test"
            let text = newText?.split(/[!,?,.,,,]/)
            if(text?.length>0){
                newText="";
                for(let i=0;i<text.length;i++){
                    if(i===text.length-1){
                        newText += text[i];
                        break;
                    }
                    newText += text[i]+"-";
                }
            }
        }
        return newText
    }

    // get no for location data
    let peopleDataWithNo = []
    let dataToExcel = []
    interviewedData?.personalInfos?.map((record,index) =>{

        let pageAdd = interviewedData?.paginator?.currentPage > 1 ? ((interviewedData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}
        peopleDataWithNo.push(setNoToPeopleData)

        let toExcelItem = {
            no:index+1,
            fullName: record?.lastName+" "+record?.firstName,
            englishName: record?.englishName === null || record?.englishName === undefined ? "" : record?.englishName,
            age: record?.age,
            gender:record?.gender,
            tel: record?.tel === null || record?.tel === undefined ? "" : record?.tel,
            province: record?.province,
            district: record?.district === "" ? "": record?.district,
            commune: record?.commune === "" ? "": record?.commune,
            village: record?.village === "" ? "": record?.village,
            patientId: splitDot(record?.patientId),
            direct: record?.affectedFrom?.direct ? "":"",
            times: record?.sampleTest?.length===undefined || record?.sampleTest?.length===0 ? '' :"" + record?.sampleTest?.length  ,
            vaccinated:record?.vaccination?.times > 0 ? "":"",
            vaccineType: record?.vaccination?.vaccineType === null || record?.vaccination?.vaccineType === undefined ? "": record?.vaccination?.vaccineType,
            nationality:record?.nationality
        }

        record?.sampleTest?.map(item=>{
            console.log(item)
            toExcelItem = {
                ...toExcelItem,
                date:  moment.parseZone(item.date).format("D/MM/YYYY") , 
                result: item.result ? "Positive":"Negative"
            }
        })

        dataToExcel.push(toExcelItem)
    })

    const dateHolder = ["", ""]


    return (
        <>
        <Form
            form={form}
            name="filter"
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            style={{width:'100%'}}
            fields={
                [
                    {
                         name:"confirm",
                        value:confirm,
                    }
                ]
            }
        >
            <Row>
                {/* <AddInterview open={openAdd} setOpen={setOpenAdd} />
                <EditInterview open={openEdit} setOpen={setOpenEdit} editData={editData}/> */}
                <Col
                    xs={24}
                    md={{ span: 2}}
                    style={{padding:3}}
                >
                    <ExportToExcel 
                        
                                headers={PeopleList} 
                                data={
                                    dataToExcel
                                } 
                                filename={"sampleTest"} 
                                title={"Export to CSV"}
                            />
                </Col>
                <Col xs={24} md={2} style={{padding:3}}>
                        <Form.Item name="limit">
                            <Select placeholder="" style={{width:"100%"}} onChange={(e) => setLimit(e)}>
                                <Option value={15}>15</Option>
                                <Option value={100}>100</Option>
                                <Option value={200}>200</Option>
                                <Option value={500}>500</Option>
                                <Option value={1000}>1000</Option>
                            </Select>
                        </Form.Item>
                        </Col>
                
                <Col
                    xs={24}
                    md={{ span: 4, offset: 0 }}
                    style={{padding:3}}
                >
                    <Form.Item name="interview">
                        <Select placeholder="" style={{ width: "100%" }} onChange={(e) => setInterviewType(e) }>
                            {/* <Option value={true}></Option> */}
                            <Option value={null}>----</Option>
                            <Option value={true}></Option>
                            <Option value={false}></Option>
                            {/* <Option value={true}></Option>
                            <Option value={false}></Option> */}
                        </Select>
                    </Form.Item>
                </Col>
                <Col
                    xs={24}
                    md={{span:4, offset:0}}
                    style={{padding:3}}
                >
                    <Form.Item name="district">
                        <ListSelect
                            type={0}
                            data={convertToDistrict(districForFilter)}
                            title="/"
                            setValue={setToDistrictFn}
                        />
                    </Form.Item>
                </Col>
                <Col
                    xs={24}
                    md={{span:4, offset:0}}
                    style={{padding:3}}
                >
                    <Form.Item name="commune">
                        <ListSelect type={1} data={convertToCommune(filterDistrict, [{district:filterDistrict,commune:"----"},...communeData])} title="/" setValue={setToCommuneFn} disabled={filterDistrict === "" || filterDistrict === null ? true : false} />
                    </Form.Item>
                </Col>
                <Col
                    xs={24}
                    md={{span:4, offset:0}}
                    style={{padding:3}}
                >
                    <Form.Item name="village">
                        <ListSelect type={1} data={convertToVillage(filterCommune, [{commune:filterCommune,village:"----"},...villageData])} title="" setValue={setToVillageFn} disabled={filterCommune === "" || filterCommune === null ? true : false} />
                    </Form.Item>
                </Col>

                <Col
                    xs={24}
                    md={{ span: 4, offset: 0}}
                    style={{padding:3}}
                >
                    
                    <Form.Item name="keyword">
                        <Input.Search
                            // onChange={(e) => setKeyword(e.target.value)}
                            placeholder="..."
                        />
                    </Form.Item>
                </Col>

                <Col xs={24} md={{ span: 4, offset: 0 }} style={{padding:5}}>
                            <Form.Item name="recovered" valuePropName="checked">
                                <Checkbox onChange={(e) => handleCheckRecover(e.target.checked)}>
                                    
                                </Checkbox>
                            </Form.Item>
                            {/* <Form.Item
                                name="recoveredAt"
                                // rules={[{ required: true, message: '!' }]}
                            >
                                <RangePicker
                                style={{ width: "100%" }}
                                placeholder={dateRecoveredHolder}
                                disabled={!recovered}
                                />
                            </Form.Item> */}
                </Col>

                

                <Col xs={24} md={{ span: 4, offset: 0 }} style={{padding:5}}>
                            <Form.Item name="death" valuePropName="checked">
                                <Checkbox onChange={(e) => handleCheckDeath(e.target.checked)}>
                                    
                                </Checkbox>
                            </Form.Item>
                            {/* <Form.Item
                                name="deathAt"
                                // rules={[{ required: true, message: '!' }]}
                            >
                                <RangePicker
                                style={{ width: "100%" }}
                                placeholder={dateDeathHolder}
                                disabled={!death}
                                />
                            </Form.Item> */}
                </Col>

                <Col
                    xs={24}
                    md={{ span: 4, offset: 0 }}
                    style={{padding:5}}
                >
                    <Form.Item name="filterDate">
                        <RangePicker
                            onChange={(e)=>onSelectDate(e)}
                            // showTime={{ format: 'HH:mm' }}
                            // format="MM-DD-YYYY HH:mm"
                            placeholder={dateHolder}
                            // onOk={onOk}
                        />
                    </Form.Item>
                </Col>

                <Col xs={24} md={{ span: 4, offset: 0 }} style={{padding:5}}>
                            <Form.Item name="isFillDate" valuePropName="checked">
                                <Select placeholder="" style={{ width: "100%" }}>
                                    {/* <Option value={true}></Option> */}
                                    <Option value={true}></Option>
                                    <Option value={false}> Lab Form</Option>
                                    {/* <Option value={true}></Option>
                                    <Option value={false}></Option> */}
                                </Select>
                            </Form.Item>
                            {/* <Form.Item
                                name="deathAt"
                                // rules={[{ required: true, message: '!' }]}
                            >
                                <RangePicker
                                style={{ width: "100%" }}
                                placeholder={dateDeathHolder}
                                disabled={!death}
                                />
                            </Form.Item> */}
                </Col>

                <Col xs={24} md={{span:4}} style={{padding:5}}>
                        <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
                        
                        </Button>
                    </Col>
                <Col xs={24} md={{span:4}} style={{padding:5}}>
                        <Button onClick={()=> showAll()} type="primary" style={{ width: "100%" }}>
                            
                        </Button>
                </Col>
            </Row>
        </Form>
        <Row>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Table
                    className="table-go-list"
                    columns={interviewCol({ handleDelete, limit, page, handleEdit })}
                    dataSource={peopleDataWithNo}
                    rowKey={record => record.id}
                    pagination={{
                        total: interviewedData?.paginator?.totalDocs,
                        //pageSizeOptions:["10", "20"],
                        // showSizeChanger: true,
                        pageSize:filterData.limit,
                        // current: interviewedData?.paginator?.currentPage,
                        onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                    }}
                    scroll={{ x: 1000 }}
                    sticky
                    loading={loading}
                />
            </Col>
        </Row>
        </>
    )
}
