
import {GET_DATA_FOR_GRAP} from '../../graphql/dashboardAndReport'
import { useQuery } from "@apollo/client";
import React, { useState, useEffect } from "react";
import { Bar,Line } from "react-chartjs-2";
import numeral from "numeral";
import { Chart } from 'react-chartjs-2';


function LineGraph({ casesType }) {
  const [data, setData] = useState({});
  const {dat,refetch} = useQuery(GET_DATA_FOR_GRAP,{onCompleted:({getDataForGrap})=>{
    setData(getDataForGrap);
  }})

let color = "rgb(167, 11, 153)";
// backgroundColor:["#ffd666","#73d13d","#cf1322","yellow"]
let caseData=data.cases;
switch(casesType){
  case "deaths": 
    color = "#cf1322"
    caseData = data.deaths
    break;
      case "recovered":
        color = "#73d13d"
        caseData = data.recovered
    break;
  default:
    color = "#ffd666"
    caseData = data.cases
    break;
}


const dd= caseData?.map(d=>d.x);
const va= caseData?.map(d=>d.y);

const datas = {
  labels: dd,
  datasets: [
    {
      label: casesType,
      data: va,
      fill: false,
      backgroundColor: color,
      borderColor: color,
      lineTension: 0.5,
    },
  ]
};
  return (
    <div>
        <Line
        data={datas}
        options={{
          maintainAspectRatio: true,
          
          plugins: {
            legend: {
                labels: {
                    // This more specific font property overrides the global property
                    font: {
                        size: 15,
                        family:"Khmer OS Siemreap"
                    },
                    // color:'#e6fffb'

                },
                
               
            },
            datalabels:{
              // display:false,
              // color:'#FFFFFF',
              anchor:'end',
              align:'top',
              font:{
                size:10
              }
            },
            //for chart title future use
            // title:{
            //   display:true,
            //   text:"test",
            //   font:{
            //     size:40
            //   }
            // },
            tooltip: {
              // enabled:false,
              // title:{
              //   display:true,
              //   fontSize:50
                // font: {
                //   size:50,
                //   family:"Khmer OS Siemreap"
                // }
              // }

              
           
            }
          },
          scales: {
            x: {
                ticks: {
                    font: {
                        size: 13,
                        family:"Khmer OS Siemreap"
                    },
                  
                }
            },
            y: {
              ticks: {
                  font: {
                      size: 13,
                      family:"Khmer OS Siemreap",
                      
                  },
                  // mirror:true,
                  
              }
            }
          },

        }}
        />
    </div>
  );
}

export default LineGraph;