import React, {useContext, useState, useEffect} from 'react'
import { Row, Col, Button, Input, Table,message } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { hospitalCol, schoolCol } from '../component/school/tableColumn/schoolColumn'
import AddHospital from '../component/hospital/modal/addHospital';
import { useQuery,useMutation } from '@apollo/client';
import { GET_ALL_HOSPITAL,ALL_HOSPIAL_INFO } from '../graphql/hospital';

import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import CustomMap from '../component/covideComponents/CustomMap';
import AddSchool from '../component/school/modal/addSchool';

export default function School() {

    const [schoolData, setSchoolData] = useState([])
      const [hospital, setHospital] = useState([]);

    const [openAdd, setOpenAdd] = useState(false)
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")

    // const { data: dat } = useQuery(ALL_HOSPIAL_INFO, {
    //     onCompleted: ({ allHospitalInfos }) => {
    //         setHospital(allHospitalInfos)
    //     },
        
    //   });

    const {data,loading,error,refetch} = useQuery(GET_ALL_HOSPITAL,{
        variables:{
            page: page,
            limit: limit,
            keyword: keyword,
        },
        onCompleted:({getHospitalInfoWithPagination})=>{
             
            setSchoolData(getHospitalInfoWithPagination)
        },
        fetchPolicy:'network-only',
    })

    // useEffect(()=>{
    //     if(data){
    //         setHospitalData(data?.getHospitalInfoWithPagination)
    //     }
    // }, [data])

    // const [deleteHospitalInfo, { loading: deleteLoading, error: deleteError }] = useMutation(DELETE_HOSPITALINFO_BY_ID, {     
    //     onCompleted: () => {
    //         refetch()
    //         message.success("")
    //     },

    // })

    // useEffect(() => {
    //     refetch()
    // }, [page, limit, keyword])


    const handleDelete = (e) => {
        //hospitalDataDispatch({type: "DELETE_HOSPITAL", payload: e})
        
    }

    return (
        <Row>
            <AddSchool open={openAdd} setOpen={setOpenAdd} setRefetch={refetch}/>

            <Col
                xs={8}
                md={18}
            >
                <Button
                    type="primary"
                    onClick={() => setOpenAdd(true)}
                >
                    
                    <PlusOutlined />
                </Button>
            </Col>
            <Col
                xs={16}
                md={6}
            >
                <Input.Search
                    onChange={(e)=>setKeyword(e.target.value)}
                    placeholder="..."
                />
            </Col>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Row>
                    <Col xs={24} md={14}>
                        <Table
                        // caseCol({handleDelete})
                            className="table-go-list"
                            columns={schoolCol({handleDelete,limit,page})}
                            dataSource={schoolData?.hospitalInfos}
                            rowKey={record => record.id}
                            pagination={{
                                total: schoolData?.paginator?.totalDocs,
                                //pageSizeOptions:["10", "20"],
                                // showSizeChanger: true,
                                current: schoolData?.paginator?.currentPage,
                                onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                            }}
                            scroll={{ x: 900 }} 
                            sticky 
                        />
                    </Col>
                    <Col xs={24} md={{span:9,offset:1}}>
                        <CustomMap district=""  zoom={8} data={hospital} type="hospital" />
                    </Col>
                </Row>
                
            </Col>
        </Row>
    )
}
