import React, { useContext, useState, useEffect } from 'react'
import { Row, Col, Button, Input, Table, message, Select, DatePicker, Card, Avatar, Skeleton, Divider, Switch } from 'antd'

import {
    districtData,communeData, villageData
} from "../context/headerContext";
import {
    convertToCommune,
    convertToDistrict,
    convertToVillage,
    joinThreeArray,
} from "../function/fn";

import { GET_DATA_FOR_BARCHART, GET_GRAPH_AGE, GET_GRAPH_BY_DAY } from '../graphql/filterchart';
import BarChart from '../component/covideComponents/BarChart';
import { useQuery } from '@apollo/client';
import { BarChartOutlined } from '@ant-design/icons';

const {Option} = Select
const { RangePicker } = DatePicker;

export default function FilterChart() {

    const dateHolder = ["", ""]
    const [barData,setBarData] = useState([]) 

    const [ageGrapData,setAgeGraphData] = useState([])
    const [dayGraphData,setDayGraphData] = useState([])

    const [isBar,setIsBar] = useState("bar")
    const [dataOf,setDataOf] = useState("")

    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const [chartType, setChartType] = useState("bar")

    const [district, setDistrict] = useState(null);
    const [commune,setCommune] = useState(false)
    const [village,setVillage] = useState(false)

    const { data,loading,refetch } = useQuery(GET_DATA_FOR_BARCHART,{
        variables:{
            district:district,
            commune:commune,
            village:village,
            startDate:ranges.startDate,
            endDate:ranges.endDate
        },
        onCompleted:({getDataForBarChart})=>{
            setBarData(getDataForBarChart)
        }
    })

    useEffect(()=>{
    if(data!==undefined){
        //  console.log(abc)
        setBarData(data?.getDataForBarChart)
    }
    },[data])

    const { data:ageData,loading:loadingAge,refetch:refetchAge } = useQuery(GET_GRAPH_AGE,{
        variables:{
            startDate:ranges.startDate,
            endDate:ranges.endDate
        },
        onCompleted:({getGraphByage})=>{
            console.log(getGraphByage,"test")
            setAgeGraphData(getGraphByage)
        }
    })

    useEffect(()=>{
    if(ageData!==undefined){
        //  console.log(abc)
        setAgeGraphData(ageData?.getGraphByage)
    }
    },[ageData])

    const { data:dayData,loading:loadingDay,refetch:refetchDay } = useQuery(GET_GRAPH_BY_DAY,{
        variables:{
            district:district,
            startDate:ranges.startDate,
            endDate:ranges.endDate
        },
        onCompleted:({getGraphByday})=>{
            
            console.log(getGraphByday)

            let confirm = getGraphByday?.confirm
            let death = getGraphByday?.death
            let recovered = getGraphByday?.recovered

            setDayGraphData(joinThreeArray(confirm,recovered,death))
        }
    })

    useEffect(()=>{
    if(dayData!==undefined){

        let confirm = dayData?.getGraphByday?.confirm
        let death = dayData?.getGraphByday?.death
        let recovered = dayData?.getGraphByday?.recovered

        setDayGraphData(joinThreeArray(confirm,recovered,death))
    }
    },[dayData])

    useEffect(()=>{
        refetch()
        refetchAge()
        refetchDay()
    },[ranges])

    const onSelectDate = (e) => {
        console.log(e)
        if(e !== null){
            let startDate = new Date(e[0])
            let endDate = new Date(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
            // console.log(endDate)
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const handleSetDistrict =(e)=>{
        // if(e!==null){
            setDataOf( e === "" ? "" + e:"" + e)
            setDistrict(e)
            setCommune(false)
            setVillage(false)
            setRanges({startDate:null,endDate:null})

            if(e ===null){
                setDataOf("")
            }
        // }else{

        // }
        
    }

    const handleSetCommune = (e)=>{
        setCommune(e)
        setVillage(false)
    }

    const handleSetVillage = (e)=>{
        setVillage(e)
        setCommune(false)

    }

    const handleSetBarChart = (e)=>{
        console.log(e)
        if(e){
            setChartType("bar")
        }else{
            setChartType("")
        }
    }

    const handleSetLineChart = (e)=>{
        console.log(e)
        if(e){
            setChartType("line")
        }else{
            setChartType("")
        }
    }

    const handleSetDateChart = (e)=>{
        console.log(e)
        if(e){
            setChartType("dateBar")
        }else{
            setChartType("")
        }
    }

    const handleSetAgeChart = (e)=>{
        console.log(e)
        if(e){
            setChartType("ageBar")
        }else{
            setChartType("")
        }
    }


    return (
        <>
        <Row>

                <Col xs={24}>
                    <Row>
                        <Col xs={24} md={4} style={{padding:3}}>
                            <button className="btn-grad-pro" type="primary" style={{ width: "100%",height:"100%" }} onClick={()=> handleSetDistrict(null)}>
                                
                            </button>
                        </Col>
                        <Col xs={24} md={20}>
                           
                            <Row>
                                {
                                    districtData?.map(record =>{
                                        return (
                                            <Col xs={24} md={4} style={{padding:3}}>
                                                <button style={{ width: "100%" }} className="btn-grad" onClick={()=> handleSetDistrict(record.district)}>
                                                    {record.district === ""? "":""}{record.district}
                                                </button>
                                            </Col>
                                        )
                                    })
                                }
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <button style={{ width: "100%" }} className="btn-grad" onClick={()=> handleSetDistrict("")}>
                                        
                                    </button>
                                </Col>
                                {/* <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>

                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col>
                                <Col xs={24} md={4} style={{padding:3}}>
                                    <Button style={{ width: "100%" }}>
                                        
                                    </Button>
                                </Col> */}
                            </Row>
                        </Col>
                    </Row>
                </Col>
                <Col xs={24}>
                <Row>
                    <Col xs={24} md={4} style={{padding:3}}>
                        <Button className="btn-grad-under" style={{ width: "100%",height:'100%' }} onClick={()=> handleSetCommune(true)} disabled={district===null}>
                            
                        </Button>
                    </Col>
                    
                    <Col
                        xs={24}
                        md={{ span: 4, offset: 0 }}
                        style={{padding:3}}
                    >
                        <Button className="btn-grad-under" style={{ width: "100%",height:'100%' }} onClick={()=> handleSetVillage(true)} disabled={district===null}>
                            
                        </Button>
                    </Col>
                    <Col
                        xs={24}
                        md={{span:4, offset:0}}
                        style={{padding:3}}
                    >
                        <button className="btn-grad-date" style={{ width: "100%" }} onClick={()=> onSelectDate([new Date(),new Date])}>
                            
                        </button>

                    </Col>
                    <Col
                        xs={24}
                        md={{ span: 4, offset: 0 }}
                        style={{padding:3}}
                    >
                            <RangePicker
                                // className="btn-grad-daterange"
                                style={{width:'100%',height:'100%'}}
                                onChange={(e)=>onSelectDate(e)}
                                placeholder={dateHolder}

                            />
                    </Col>

                </Row>
                </Col>
            </Row>
        <Row>
          
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Divider />
                {/* { actionData?.map(res=>{
                    return  <ActionCard  actionType={res?.type} username={res?.username} date={res?.date} actionDescription={res?.userAction} loading={loading} timeout={timerOut} />
                })
                    
                } */}
                <div style={{overflowX:'scroll'}}>
                    <div className="graph" style={{width: village ? '250%':'100%',backgroundColor:'#262626'}}>
                        <h2 className="covid_table" style={{color:'#FFFFFF'}}>
                            <span style={{backgroundColor:'#69c0ff',textAlign:'center',padding:7,width:100,height:100,borderRadius:50}}>
                                <BarChartOutlined style={{fontSize:30,color:'#FFFFFF', width:40,height:100}}/>
                            </span>
                            &emsp; <b>{dataOf}</b>
                        </h2><br/>

                        <Switch checkedChildren="" unCheckedChildren="" onChange={(e)=> handleSetDateChart(e)} checked={chartType==="dateBar"}/>&emsp;
                        <Switch checkedChildren="" unCheckedChildren="" onChange={(e)=> handleSetAgeChart(e)} checked={chartType==="ageBar"}/>
                        <br/><br/>

                        <Switch checkedChildren="" unCheckedChildren="" onChange={(e)=> handleSetBarChart(e)} checked={chartType==="bar"} />&emsp;
                        <Switch checkedChildren="" unCheckedChildren="" onChange={(e)=> handleSetLineChart(e)} checked={chartType==="line"}/> <br/>

                        

                        <BarChart 
                            data={
                                chartType==="ageBar" ? ageGrapData:
                                    chartType==="dateBar" ? dayGraphData:barData
                            } 
                            isBar={chartType} 
                        />
                    </div>
                </div>
            </Col>
        </Row>
        </>
    )
}
