import {gql} from '@apollo/client'

export const GET_SHOP_WITH_PAGINATION = gql`
query getShopWithPagination(
    $limit:Int,
    $keyword:String,
    $page:Int,
    $province:String,
    $commune:String,
    $village:String,
    $district:String,
    $marketName:String
    ){
    getShopWithPagination(
        limit:$limit,
        keyword:$keyword,
        page:$page,
        province:$province,
        commune:$commune,
        village:$village,
        district:$district,
        marketName:$marketName
    ){
      shops{
              shopNumber
        _id
        name
              registrationNumber
              acknowledgeAs
              registerDate
              village
              commune
              district
              province
              firstName
              lastName
              personalInfoId{
          id
          firstName
          lastName
              }
              tel
      }
      paginator{
          slNo
          prev
          next
          perPage
          totalPosts
          totalPages
          currentPage
          hasPrevPage
          hasNextPage
          totalDocs
      }
    }
  }
`

export const DELETE_SHOP_BY_ID = gql`
mutation deleteShop($shopId:ID){
    deleteShop(shopId:$shopId){
        success
        message
    }
}
`

export const GET_SHOP_BY_ID = gql`
query getShopById($shopId:ID!){
    getShopById(shopId:$shopId){
        shopNumber
        _id
        name
        registrationNumber
        acknowledgeAs
        registerDate
        village
        commune
        district
        province
        firstName
        lastName
        marketName
        personalInfoId{
            id
            firstName
            lastName
            tel
        }
        tel
    }
}
`

export const UPDATE_SHOP_BY_ID = gql`
mutation updateShop(
    $name: String,
    $shopNumber: String,
    $registrationNumber: String,
    $acknowledgeAs: String,
    $registerDate: DateTime,
    $village: String,
    $commune: String,
    $district: String,
    $province: String,
    $firstName: String,
    $lastName: String,
    $personalInfoId: ID,
    $tel: String,
    $marketName:String,
    $shopId:ID
){
  updateShop(shopId:$shopId,updatedShop:{
    name:$name
    shopNumber:$shopNumber
    registrationNumber:$registrationNumber
    acknowledgeAs:$acknowledgeAs
    registerDate:$registerDate
    village:$village
    commune:$commune
    district:$district
    province:$province
    firstName:$firstName
    lastName:$lastName
    personalInfoId:$personalInfoId
    tel:$tel,
    marketName:$marketName
  }){
    success
    message
  }
}
`

export const GET_PEOPLE_BY_SHOP = gql`
query getAffectedPeopleByShopIdWithPagination(
    $shopId:ID!,
    $marketName:String,
    $keyword:String,
    $page:Int,
    $limit:Int,
    $startDate:DateTime,
    $endDate:DateTime,
){
  getAffectedPeopleByShopIdWithPagination(
    shopId:$shopId,
    marketName:$marketName,
    keyword:$keyword,
    page:$page,
    limit:$limit,
    startDate:$startDate,
    endDate:$endDate,
  ){
    transactions{
      personalInfoId{
        id
        firstName
        lastName
        tel
        currentState{
          confirm
          recovered
          death
        }
      }
      createdAt
    }
    paginator{
        slNo
        prev
        next
        perPage
        totalPosts
        totalPages
        currentPage
        hasPrevPage
        hasNextPage
        totalDocs
    }
  }
}
`

