import { gql } from "@apollo/client";

export const GET_DATA_FOR_BARCHART = gql`
query getDataForBarChart($district:String,$commune:Boolean,$village:Boolean,$startDate:DateTime,$endDate:DateTime){
    getDataForBarChart(district:$district,commune:$commune,village:$village,startDate:$startDate,endDate:$endDate){
      _id
      confirm
      recovered
      death
    }
  }
`

export const GET_GRAPH_AGE = gql`
query getGraphByage($startDate:DateTime,$endDate:DateTime){
  getGraphByage(startDate:$startDate,endDate:$endDate){
    label
    confirm
    recovered
    death
  }
}
`

export const GET_GRAPH_BY_DAY = gql`
query getGraphByday($startDate:DateTime,$endDate:DateTime,$district:String){
  getGraphByday(startDate:$startDate,endDate:$endDate,district:$district){
    death{
      _id
      death
    }
    confirm{
      _id
      confirm
    }
    recovered{
      _id
      recovered
    }
  }
}
`