import React, { useContext, useState, useEffect } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, Divider, DatePicker, message, Checkbox,List,Typography, Popconfirm, Tooltip } from 'antd'
import { provinceData, districtData, communeData, villageData, genderData, nationalityData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import {DeleteOutlined} from '@ant-design/icons'
import { convertToCommune, convertToDistrict, convertToVillage, convertPersonalToSelect } from '../../../function/fn'
import { PeopleController } from '../../../context/peopleContext'
import { useQuery, useMutation, empty } from '@apollo/client'
import { GET_ALL_CASES_NO_LIMIT } from '../../../graphql/case'
import { CREATE_NEW_PERSON } from '../../../graphql/people' 
import { setAddPeople } from '../../../function/set'
import AddCase from '../../case/modal/addCase'
import moment from 'moment'
import { ADD_SELLER } from '../../../graphql/seller'

const { Option } = Select

export default function AddSeller({ open, setOpen,setRefetch, personalInfoId, setPersonalInfoId, personalInfoData}) {
    let [form] = Form.useForm()
    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")

    const [caseId, setCaseId] = useState("")
    const [caseData, setCaseData] = useState({})
    const [allCases, setAllCases] = useState([])

    const [shopData,setShopData] = useState([])

    const [patientId, setPatientId] = useState("None")

    const [openModal, setOpenModal] = useState(false)

    const [createPersonalInfo, { loading: loadingCreate, error: errorCreate }] = useMutation(ADD_SELLER, {
        onCompleted: ({ createPersonalInfo }) => {
            if (personalInfoId === "new") {
                if(createPersonalInfo.response.success=== false){
                    setPeopleSampleFn()
                    message.error(createPersonalInfo.response.message)
                }else{                   
                    setPersonalInfoId(createPersonalInfo.personalInfo)
                    message.success("!")

                    setOpen(false)
                    form.resetFields()
                }
            } else {
                if(createPersonalInfo.response.success=== false){
                    message.error(createPersonalInfo.response.message)
                }else{                   
                    setRefetch()
                    message.success("!")

                    setOpen(false)
                    form.resetFields()
                }
            }

        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const onFinish = (values) => {
        // console.log('Success:', setAddPeople(values));

        console.log(values)

        createPersonalInfo({variables: {
            ...values, 
            dob: moment(values.dob),
            age: parseInt(values.age), 
            village: values.village === undefined ? "" : values.village, 
            district: values.district === undefined ? "" : values.district, 
            commune: values.commune === undefined ? "" : values.commune, 
            seller:true,
        }})

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToGenderFn = (e) => {
        form.setFieldsValue({
            gender: e
        });
    };

    const setToNationalityFn = (e) => {
        form.setFieldsValue({
            nationality: e
        });
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };
    
    const setPeopleSampleFn = () => {
        setPersonalInfoId("")
        setOpen(false)
        // console.log(personalInfoId)
    }


    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => personalInfoId === "new" ? setPeopleSampleFn() : setOpen(false)}
            // onCancel={() => setOpen(false)}
            footer={null}
            
        >
            {/* <AddCase open={openModal} setOpen={setOpenModal} caseId={caseId} setCaseId={callbackCase} /> */}
            <Form
                form={form}
                name="addPeople"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                
            >
                <Row>
                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="patientId"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="lastName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="firstName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="englishName"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="gender"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={0} data={genderData} title="" setValue={setToGenderFn} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="age"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="nationality"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={0} data={nationalityData} title="" setValue={setToNationalityFn} />
                        </Form.Item>
                    </Col>



                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    {/* <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="chronic"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

                    

                    {/* <Col xs={24} md={{ span: 11, offset:2 }}>
                        <Form.Item>
                        <Button
                            // htmlType="submit"
                            // type="primary"
                            onClick={(e)=> handleAddVaccination(e)}
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item>
                        <List
                            
                            bordered
                            dataSource={shopData}
                            
                            renderItem={(item,index) => (
                                
                                <List.Item
                                    actions={

                                            [
                                            <Tooltip className="deleteVacction" title="">         
                                                <Popconfirm
                                                    title="?"
                                                    onConfirm={() => { handleDeleteVaccinationData(index) }}
                                                    okText=""
                                                    cancelText=""
                                                >
                                                    <span style={{ color: "red" }}><DeleteOutlined /></span>
                                                </Popconfirm>
                                            </Tooltip>
                                            ]
                          
                                    }        
                                    placeholder={[{empty:""}]}                        
                                >
                                <Row>
                                    <Col xs={24} md={24}>
                                        <Typography.Text mark>[{item?.shopId}]</Typography.Text> {item.shopName}, {item.shopType}
                                    </Col>

                                </Row>
                                </List.Item>
                            )}
                        />
                        </Form.Item>
                    </Col> */}


                    <Col xs={24}>
                        <Form.Item>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
