import React, { useContext, useState, useEffect } from 'react'
import { Form, Modal, Input, Row, Col, Button, Select, DatePicker, message } from 'antd'
import { setEditSubQuarantine } from '../../../function/set'
import { useMutation, useQuery } from '@apollo/client'
import { ALL_QUARANTINEINFO, CREATE_PERSON_QUARANTINE, GET_QUARANTINE_BY_ID, UPDATE_PEOPLE_BY_QUARANTINE } from '../../../graphql/quarantine'
import moment from 'moment'
import { ListSelect, ListSelectTab } from '../../../static/own-comp'
import { convertQurantineToSelect } from '../../../function/fn'
import { UPDATE_PERSONALINFO_QUARANTINE } from '../../../graphql/people'
const { Option } = Select

export default function AddPeopleQuarantine({ open, setOpen, data, peopleData, setRefetch, quarantineData }) {
    // console.log(data)
    let [form] = Form.useForm()

    const [quarantineId, setQuarantineId] = useState("")
    const [quarantine, setQuarantine] = useState({})
    const [openModal, setOpenModal] = useState(false)
    const [quarantineInfoData, setQuarantineInfoData] = useState({})

    const {data:quarantineD, loading:loadingQInfo, refetch:refetchQuarantine} = useQuery(GET_QUARANTINE_BY_ID,{
        variables:{
            id:quarantineId
        },
        onCompleted:({getQuarantineInfoById})=>{
            // console.log(getQuarantineInfoById,"testing get quaritine")
            setQuarantineInfoData(getQuarantineInfoById)
        }
    })

    useEffect(()=>{
        if(quarantineD!== undefined){
            setQuarantineInfoData(quarantineD?.getQuarantineInfoById)
        }
    },[quarantineD])

    const { data: quarantineInfo, loading: loadingQuarantine, error } = useQuery(ALL_QUARANTINEINFO, {
        onCompleted: ({ allQuarantineInfos }) => {

        }
    })

    const allQuarantineInfos = quarantineInfo?.allQuarantineInfos
    
    const [updatePeopleFromQuarantine,{loading}]=useMutation(UPDATE_PERSONALINFO_QUARANTINE,{
        onCompleted:({updatePeopleFromQuarantine})=>{
            if(updatePeopleFromQuarantine.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(updatePeopleFromQuarantine.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const [addPeopleToQuarantine,{data:addData}]=useMutation(CREATE_PERSON_QUARANTINE,{
        onCompleted:({addPeopleToQuarantine})=>{
            if(addPeopleToQuarantine.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(addPeopleToQuarantine.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    useEffect(() => {

        if(data!== undefined){
            let lengthQuaranting = data?.quaranting?.length
            let quaranting = data?.quaranting

            form.setFieldsValue({
                ...data,
                date_in: data?.quaranting?.date_in === null || data?.quaranting?.date_in === undefined ? null : moment.parseZone(data?.quaranting?.date_in),
                date_out: data?.quaranting?.date_out === null || data?.quaranting?.date_out === undefined ? null : moment.parseZone(data?.quaranting?.date_out),
                quarantineInfo:data?.quaranting?.quarantineInfo,
                personTypes: data?.quaranting?.personTypes,
                coorporate: data?.quaranting?.coorporate,
                quarantineInfo: quaranting?.quarantineInfo
            })

            setQuarantineId(quaranting?.quarantineInfo)
        }
    }, [data,open])

    const onFinish = (values) => {
        let quaranting = data.quaranting
        let quarantineIndex = allQuarantineInfos.findIndex(e=> e.id === values.quarantineInfo)

        let quarantineName = allQuarantineInfos[quarantineIndex].locationName

        if(quaranting!==undefined){
            var array = {
                ...values,
                // description: values.description === undefined ? null : values.description,
                date_in: moment(values.date_in).format(),
                date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
                personalInfoId:data.peopleId,
                quarantineInfoInnerId:quaranting.id,
                locationName:  quarantineInfoData.locationName,
                quarantineInfo:values.quarantineInfo,
                totalRoomate: parseInt(quaranting.totalRoomate),
                roomNumber:quaranting.roomNumber,
                locationType: quarantineInfoData.locationType,
                long: quarantineInfoData.long === undefined ? 0 : parseFloat(quarantineInfoData.long),
                lat: quarantineInfoData.lat === undefined ? 0 : parseFloat(quarantineInfoData.lat),
            }
    
            updatePeopleFromQuarantine({
                variables:array
            })
        }else{
            var array = {
                ...values,
                date_in: moment(values.date_in).format(),
                date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
                personalInfo:data.peopleId,
                quarantineInfo:values.quarantineInfo,
                locationName: quarantineInfoData.locationName,
                long: quarantineInfoData.long === undefined ? 0 : parseFloat(quarantineInfoData.long),
                lat: quarantineInfoData.lat === undefined ? 0 : parseFloat(quarantineInfoData.lat),
                locationType:quarantineInfoData.locationType
            }
    
            addPeopleToQuarantine({variables:array})
        }
        
        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToQuarantineFn = (e) => {

        form.setFieldsValue({
            quarantineInfo: e
        });
        if (e === "new") {
            setOpenModal(true)
        }
        setQuarantineId(e)

    };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}

        >
            <Form
                form={form}
                name="editSubQuarantine"
                // initialValues={setEditSubQuarantine(data)}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="quarantineInfo"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelectTab type={6} data={convertQurantineToSelect(allQuarantineInfos)} title="" setValue={setToQuarantineFn} disabled={quarantineId === "new" ? true : false} />
                            {/* <ListSelect type={6} data={convertQurantineToSelect(allQuarantineInfos)} title="" setValue={setToQuarantineFn} disabled={quarantineId === "new" ? true : false} /> */}
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="personTypes"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="date_in"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="date_out"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="coorporate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="others"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
