import { Space, Popconfirm,Tooltip } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined,
    QrcodeOutlined
} from '@ant-design/icons';
import { AiOutlineShop } from 'react-icons/ai'
import {GiShop} from 'react-icons/gi'
import { Link } from 'react-router-dom';

export const shopCol = ({handleDelete,limit,page,handleGenerateQR,handleOpenAddShop,roles}) => {
    let no = 0 + (page * limit) - limit

    console.log(roles)
    
    var array = [
        {
            title: '.',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            // render: (text, record, i) => (
            //     <span>
            //         {no+= 1}
            //     </span>
            // ),
        },
        {
            title: '',
            dataIndex: 'shopNumber',
            key: 'shopNumber',
            width: 100,
        },
        
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 100,
           
        },
        {
            title: '',
            dataIndex: 'seller',
            key: 'seller',
            width: 130,
            render: (text, record) => (
                <span>
                    {record.personalInfoId?.lastName +" "+ record.personalInfoId?.firstName}
                </span>
            ),
        },
        
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 200,
            render: (text, record) => (
                <span>
                    {(record.village !== "") && record.village+", "}
                    {record.commune !== "" && record.commune+", "}
                    {record.district !== "" && record.district+", "}
                    {record.province && record.province}
                </span>
            ),
        },
    
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 100,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {
                        // PRINT_QRCODE
                    }
                    <Tooltip
                        title=" QR Code"
                    >
                        <span className="link" ><QrcodeOutlined onClick={()=> handleGenerateQR(record)} /></span>
                    </Tooltip>
                    
                    <Link className="link" to={"/subShop/" + record?._id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record?._id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                    {/* <Tooltip
                        title=""
                    >
                        
                        <span className="link" ><AiOutlineShop style={{fontSize:18}} onClick={()=> handleOpenAddShop(record)} /></span>
                    </Tooltip> */}

                </Space>
            ),
        }
    ]
    return array
}