import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, Divider, DatePicker, message, Checkbox,List,Typography, Popconfirm, Tooltip } from 'antd'
import { ListSelect, SelectPeople } from '../../../static/own-comp'
import { provinceData, districtData, communeData, villageData, genderData, nationalityData } from '../../../context/headerContext'
import { convertToCommune, convertToDistrict, convertToVillage, convertPersonalToSelect } from '../../../function/fn'
import { useMutation, useQuery } from '@apollo/client'
import { CREATE_SHOP, GET_SELLER_WITH_PAGINATION } from '../../../graphql/seller'
import moment from 'moment'
import AddSeller from '../../seller/modal/addSeller'
import { UPDATE_SHOP_BY_ID } from '../../../graphql/shop'
import { GET_PERSONALINFO_BY_ID } from '../../../graphql/people'

const {Option} = Select

export default function EditShop({setOpen,open,setRefetch,personalInfo,data,shopId}) {
    
    let [form] = Form.useForm()
    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")
    const [personalInfoId, setPersonalInfoId] = useState("");
    const [openAddSeller,setOpenAddSeller] = useState(false)
    const [personalInfoData,setPersonalInfoData] = useState({})

    const [page,setPage] = useState(1)
    const [limit,setLimit] = useState(200)
    const [keyword, setKeyword] = useState(data?.personalInfoId?.lastName)

    const [personalAllData,setPersonalAllData] = useState([])

    const {data:person} = useQuery(GET_SELLER_WITH_PAGINATION,{
      variables:{
        page:page,
        limit:limit,
        keyword:keyword,
        isSeller:false
      },
      onCompleted:({getSellerWithpagination})=>{
        // console.log(getSellerWithPagination)
        setPersonalAllData(getSellerWithpagination?.personalInfos)
      }
    })

    useEffect(()=>{
    
        if(person){
          setPersonalAllData(person?.getSellerWithpagination?.personalInfos)
        }
    },[keyword,person])

    const {data:personById,refetch} = useQuery(GET_PERSONALINFO_BY_ID,{
        variables:{
            id:personalInfoId
        },
        onCompleted:({getPersonalInfoById})=>{
            // console.log(getPersonalInfoById)
            setPersonalInfoData(getPersonalInfoById)
        }
    })

    useEffect(()=>{
        refetch()
    },[personalInfoId])

    const [updateShop,{loading}] = useMutation(UPDATE_SHOP_BY_ID,{
        onCompleted:({updateShop})=>{
            if(updateShop.success=== false){
                message.error(updateShop.message)
            }else{                   
                setRefetch()
                message.success("!")

                setPersonalInfoId("")
                setPersonalInfoData({})
                setOpen(false)
                form.resetFields()
            }
        },
        onError: (error) => {
            message.error(error.message)
        }
        
    })

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };

    const onFinish = (values) => {
        updateShop({
            variables:{
                ...values,
                registerDate:values.registerDate!== undefined && values.registerDate!==null ? moment(values.registerDate):null,
                personalInfoId: personalInfoId==="new" ? personalInfoData?.id:values.personalInfoId,
                firstName: personalInfoData ? personalInfoData?.firstName : data?.firstName,
                lastName: personalInfoData ? personalInfoData?.lastName : data?.lastName,
                village: values.village === undefined ? "" : values.village, 
                district: values.district === undefined ? "" : values.district, 
                commune: values.commune === undefined ? "" : values.commune, 
                shopId:shopId
            }
        })
       
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    useEffect(() => {
        form.resetFields()
    }, [open])

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
          personalInfoId: e,
        });
        if (e === "new") {
          setOpenAddSeller(true);
        }
    
        setPersonalInfoId(e);
    };

    const handleSearchPeople = (e) => {
        if (e) {
          setKeyword(e)
        } else {
          setPersonalAllData([])
        }
    };

    const callbackPersonalInfo = (e) => {
        if (e === "") {
          setPersonalInfoId(e);
          form.setFieldsValue({
            personalInfoId: null,
          });
        } else {
            setPersonalInfoData(e);
            form.setFieldsValue({
                personalInfoName: e.lastName + " " + e.firstName,
            });
        }
    };

    useEffect(()=>{
        if(data!== undefined){
            console.log(data)
            form.setFieldsValue({
                personalInfoId:data?.personalInfoId?.id,
                registerDate: data?.registerDate !== undefined && data?.registerDate !== null ? moment(data?.registerDate):null,
                shopNumber:data?.shopNumber,
                name:data?.name,
                acknowledgeAs:data?.acknowledgeAs,
                registrationNumber:data?.registrationNumber,
                tel:data?.tel,
                province:data?.province,
                district:data?.district,
                commune:data?.commune,
                village:data?.village,
                marketName:data?.marketName
            })

            setProvince(data?.province)
            setDistrict(data?.district)
            setCommune(data?.commune)

            setKeyword(data?.personalInfoId?.lastName)
            setPersonalInfoData(data?.personalInfoId)
        }
    },[data,open])

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            // onCancel={() => setOpen(false)}
            footer={null}
            
        >
            <AddSeller open={openAddSeller} setOpen={setOpenAddSeller} personalInfoId={personalInfoId} setPersonalInfoId={callbackPersonalInfo} />
            <Form
                form={form}
                name="editShop"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                
            >
            <Row>
                    <Col 
                        xs={24} 
                        md={
                        personalInfoId === "new"
                          ? { span: 11, offset: 0 }
                          : { span: 24, offset: 0 }
                        }
                    >
                        <Form.Item
                            name="personalInfoId"
                            rules={[{ required: true, message: '!' }]}
                        >
                           <SelectPeople data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} />
                        </Form.Item>
                    </Col>

                    {personalInfoId === "new" ? (
                        <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item name="personalInfoName">
                            <Input
                            disabled={true}
                            style={{ backgroundColor: "white", color: "black" }}
                            />
                        </Form.Item>
                        </Col>
                    ) : null}

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="marketName"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="shopNumber"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="name"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="registrationNumber"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input style={{width: "100%"}} placeholder="registrationNumber"/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:0 }}>
                        <Form.Item
                            name="acknowledgeAs"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="acknowledgeAs" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="registerDate"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="registerDate" style={{width:'100%'}} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11, offset: 0 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 0 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    <Col xs={24}>
                        <Form.Item>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                            loading={loading}
                        >
                            
                        </Button>
                        </Form.Item>
                    </Col>
                </Row>
                </Form>
        
        </Modal>
    )
}
