import { Image, Modal ,Button} from 'antd'
import React,{useState,useEffect, useRef} from 'react'
import QRCode from 'qrcode'
import { useHistory, useLocation, useRouteMatch } from 'react-router'
import ReactToPrint from 'react-to-print'
import QrCodePrint from '../../print/qrCodePrint'

export default function QrCodeShow({open,setOpen,data}) {

    const [src,setSrc] = useState("")
    const {origin} = window

    const printRef = useRef()

    const qrCodeLink = "https://srscanner.srprovince.com"

    var opts = {
       
        color: {
          dark:"#000000",
          light:"none"
        }
    }

    useEffect(()=>{
        if(data){
            QRCode.toDataURL(`${qrCodeLink}/${data?._id}`,opts).then((data)=>{
                setSrc(data)
            })
        }

    },[data])

    return (
        <Modal
            title="QR Code"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            width={600}
            footer={[
                <ReactToPrint
                    key="print"
                    trigger={() => <Button type="primary" ></Button>}
                    content={() => printRef.current}
                />
            ]}
            onCancel={() => setOpen(false)}
        >
            {/* <Image src={src} preview={false} /> */}
            <QrCodePrint ref={printRef} data={data} qrSrc={src}/>
        </Modal>
    )
}
