import React, { Component } from 'react'
// import Logo from '../../asset/srLogo.png'
import {Row, Col,Image} from 'antd'
import {getKhmerDate} from '../../function/fn'
import Logo from '../../asset/qr-srlogo.png'
import GoGlobal from '../../asset/goglobalschool.png'
import BackQr from '../../asset/back-qr.png'
import {FcShop} from 'react-icons/fc'

export default class QrCodePrint extends Component {
    render() {
        let data = this.props.data
        let qrSrc = this.props.qrSrc

        return (
            <Row
                style={{
                    textAlign:'center',
                    position:'relative',
                    height:'100%',
                    // backgroundColor:'#DDE7F1'
                }}
            >
                
                <Col
                    xs={24}
                    style={{
                        padding: 10,
                        backgroundImage:`url(${BackQr})`,
                        backgroundRepeat:'no-repeat',
                        backgroundPosition:'50% 100%',
                    }}
                >
                    {/* <div className="bg-over-qr">
                    
                    </div> */}
                    <center>
                    <Image style={{width:150}} src={Logo} preview={false} />
                    </center>

                    <h2 className="HeaderFont" style={{fontSize:18,color:'#0A2BB1'}}></h2>
                    <h2 className="HeaderFont" style={{fontSize:18,color:'#0A2BB1'}}><FcShop /> {data?.name}</h2>
                    
                    {/* <div style={{width:350,height:350,boxShadow: 'rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px'}}> */}
                        <Image style={{width:320,boxShadow: 'rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px'}} src={qrSrc} preview={false} />
                    {/* </div> */}
                    
                    <br/><br/>

                    <h2 className="HeaderFont" style={{fontSize:18,color:'#0A2BB1'}}>!</h2><br/>
                    <br/><br/><br/><br/><br/><br/><br/>
                    {/* <h4 style={{fontSize:14,color:'#0A2BB1'}}></h4>
                    <Image style={{width:70}} src={Logo} preview={false} /> */}
                    {/* <Image style={{width:70}} src={GoGlobal} preview={false} /> */}
                    
                </Col>
            </Row>
        )
    }
}
