import { Space, Popconfirm,Tooltip } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined,
    QrcodeOutlined
} from '@ant-design/icons';
import { AiOutlineShop } from 'react-icons/ai'
import {GiShop} from 'react-icons/gi'
import { Link } from 'react-router-dom';
import moment from 'moment';
import { getKhmerTime } from '../../../function/fn';
import { useQuery } from '@apollo/client';
import { GET_ROLES } from '../../../graphql/auth';


export const subShopCol = () => {

    var array = [
        {
            title: '.',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            // render: (text, record, i) => (
            //     <span>
            //         {no+= 1}
            //     </span>
            // ),
        },
        // {
        //     title: '',
        //     dataIndex: 'idCard',
        //     key: 'idCard',
        //     width: 130,
        // },
        
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 130,
            render: (text, record) => (
                <span size="middle">
                    {
                        record?.personalInfoId?.lastName+" "+record?.personalInfoId?.firstName
                    }
                </span>
            ),
        },

        // {
        //     title: '',
        //     dataIndex: 'address',
        //     key: 'address',
        //     width: 250,
        //     render: (text, record) => (
        //         <span>
        //             {(record.village !== "") && record.village+", "}
        //             {record.commune !== "" && record.commune+", "}
        //             {record.district !== "" && record.district+", "}
        //             {record.province && record.province}
        //         </span>
        //     ),
        // },
        {
            title: '',
            dataIndex: 'createdAt',
            key: 'createdAt',
            width: 150,
            render: (text, record) => (
                <span size="middle">
                    {moment(record?.createdAt).format("DD MM YYYY") }  {getKhmerTime(record?.createdAt)}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
            render: (text, record) => (
                <span size="middle">
                    {
                        record?.personalInfoId?.tel
                    }
                </span>
            ),
        },

        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 60,
            render: (text, record) => {
                let state ="";
                let classN = "badge-success"
                let c_state = record?.personalInfoId?.currentState;

                if(c_state.confirm===true){
                    state = ""
                    classN= "badge-purple"
                }

                if(c_state.recovered===true){
                    state = ""
                    classN= "badge-lightgreen"
                }

                if(c_state.death===true){
                    state = ""
                    classN= "badge-error"
                }

                return (
                    <span className={classN}>{state}</span>
                )
            }
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 100,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {
                       record?.personalInfoId?.id!==null ?
                       <Link className="link" to={"/subBuyer/" + record?.personalInfoId?.id}><EyeOutlined  /></Link>
                       :null
                    }
                    
                    {/* <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]
    return array
}