import React, { useContext, useState, useEffect } from 'react'
import { Row, Col, Button, Input, Table, message, Select, DatePicker, Card, Avatar, Skeleton, Divider } from 'antd'
import { PlusOutlined,FileDoneOutlined, FileExcelOutlined, FileAddOutlined } from '@ant-design/icons';
import { PeopleController } from '../context/peopleContext';
import { interviewCol } from '../component/interview/tableColumn/interviewColumn';
import AddInterview from '../component/interview/modal/addInterview';
import EditInterview from '../component/interview/modal/editInterview';
import { useSubscription} from '@apollo/client';
import { DELETE_PERSONALINFO_BY_ID, GET_INTERVIEW } from '../graphql/people';
import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import { ListSelect } from '../static/own-comp'
import {
    districForFilter
} from "../context/headerContext";
import {
    convertToCommune,
    convertToDistrict,
    convertToVillage,
} from "../function/fn";
import ExportToExcel from "../component/export/exportToExcel";
import {PeopleList} from '../asset/exportExcel/reportExcel'
import moment from 'moment'
import Meta from 'antd/lib/card/Meta';
import Title from 'antd/lib/skeleton/Title';
import ActionCard from '../component/action/actionCard';
import { formatMs } from '@material-ui/core';
import { SUB_GET_ACTION_USER } from '../graphql/action';

const {Option} = Select
const { RangePicker } = DatePicker;

export default function Action() {
    
    const [actionData,setActionData] = useState([]) 
    const [interviewType, setInterviewType] = useState(false)

    const [timerOut, setTimerOut] = useState(3000)

    const [filterDistrict, setFilterDistrict] = useState("")
    const { data,loading } = useSubscription(SUB_GET_ACTION_USER,{
        onSubscriptionComplete:({userActionWithPersonalInfo})=>{
            console.log(userActionWithPersonalInfo)
        }
    });
 

    useEffect(()=>{
    if(data!==undefined){
     let abc = [...actionData,data.userActionWithPersonalInfo];
     
     setActionData(abc)
    }
    },[data])
    return (
        <Row>
            {/* <AddInterview open={openAdd} setOpen={setOpenAdd} />
            <EditInterview open={openEdit} setOpen={setOpenEdit} editData={editData}/> */}
           
            <Col xs={24} md={4} style={{padding:3}} >
                        <Select placeholder="" defaultValue={timerOut} style={{width:"100%"}} onChange={(e) => setTimerOut(e)}>
                            <Option value={1000}>1 </Option>
                            <Option value={2000}>2 </Option>
                            <Option value={3000}>3 </Option>
                            <Option value={5000}>5 </Option>
                            <Option value={60000}>1 </Option>
                        </Select>
                    </Col>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Divider />
                { actionData?.map(res=>{
                    return  <ActionCard  actionType={res?.type} username={res?.username} date={res?.date} actionDescription={res?.userAction} loading={loading} timeout={timerOut} />
                })
                    
                }
               
            </Col>
        </Row>
    )
}
