import React, {useContext, useState,useEffect} from 'react'
import { Row, Col, Button, Input, Table, message,Select, DatePicker } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { useQuery,useMutation } from '@apollo/client';
import { 
    DELETE_PERSONALINFO_BY_ID, 

} from '../graphql/people';
import { useLocation, useRouteMatch } from 'react-router-dom';
import moment from 'moment';

import AddSeller from '../component/seller/modal/addSeller';
import { sellerCol } from '../component/seller/tableColumn/sellerColumn';
import QrCodeShow from '../component/seller/modal/qrCode';
import AddShop from '../component/shop/modal/addShop';
import { GET_SELLER_WITH_PAGINATION } from '../graphql/seller';
import { DELETE_SHOP_BY_ID, GET_SHOP_WITH_PAGINATION } from '../graphql/shop';
import { shopCol } from '../component/shop/tableColumn/shopColumn';

const {Option} = Select
const {RangePicker} = DatePicker

export default function Shop() {
    let location = useLocation()

    const [covidTypeState,setCovidTypeState] = useState(true)

    const [qrCodeData,setQRCodeData] = useState({})
    const [openQRCode,setOpenQRCode] = useState(false)
    const [openAddShop,setOpenAddShop] = useState(false)

    const [personalInfo,setPersonalInfo] = useState({})

    const [openAdd, setOpenAdd] = useState(false)
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    const [province,setProvince] = useState("")
    const [commune,setCommune] = useState("")
    const [district,setDistrict] = useState("")
    const [village,setVillage] = useState("")
    const [marketName,setMarketName] = useState("")

    const [filterCurrentState, setCurrentState] = useState(null)
    const [covidType,setCovidType] = useState("")
    const [shopData, setShopData] = useState([])
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })   

    useEffect(() => {
        if(location?.state !== undefined ){
            setCurrentState(location?.state?.status)

            if(location?.state?.status ===""){
                setCovidTypeState(false)
                setCovidType(location?.state?.covidType)
            }
            
            setRanges({
                startDate:location?.state?.date,
                endDate:location?.state?.date
            })
        }
    }, [location])

    const {data,loading,error,refetch} = useQuery(GET_SHOP_WITH_PAGINATION,{
        variables:{
            page:page,
            limit:limit,
            keyword:keyword,
            province:province,
            district:district,
            commune:commune,
            village:village,
            marketName:marketName,
        },
        onCompleted:({getShopWithPagination})=>{
            console.log(getShopWithPagination)
            setShopData(getShopWithPagination)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only",
    })

    useEffect(() => {
        refetch()
    }, [page,limit,keyword,filterCurrentState,covidType,ranges])

    useEffect(()=>{
        if(data!==undefined){
            setShopData(data?.getShopWithPagination)
        }
    },[data])

    const [deleteShop,{loading:deleteLoading, error:deleteEorror}] = useMutation(DELETE_SHOP_BY_ID,{
        onCompleted:({deleteShop})=>{
            if(deleteShop.success===true){
                refetch()
                message.success("")
            }else{
                message.error(deleteShop.message)
            }
            
        },
        onError:(errors)=>{
            message.error(errors.message)
        }
    })

    const handleDelete = (e) => {
        deleteShop({
            variables:{
                shopId:e
            }
        })
    }

    const setFilterCurrentState=(e)=>{
        // console.log(e)
        setCurrentState(e)
        if(e ===""){
            setCovidTypeState(false)
        }else{
            setCovidType("")
            setCovidTypeState(true)
        }
    }

    const setFilterCovidType=(e)=>{
        // console.log(e)
        setCovidType(e)
    }

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const handleGenerateQR =(e)=>{
        setQRCodeData(e)
        setOpenQRCode(true)
    }

    // get no for location data
    let shopDataWithNo = []

    shopData?.shops?.map((record,index) =>{
       
        let pageAdd = shopData?.paginator?.currentPage > 1 ? ((shopData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToshopData = {...record,no:index+pageAdd}
        shopDataWithNo.push(setNoToshopData)

    })

    const handleOpenAddShop =(e)=>{
        setPersonalInfo(e)
        setOpenAddShop(true)
    }

    const dateHolder = ["", ""]

    return (
        <Row>
            {/* <AddSeller open={openAdd} setOpen={setOpenAdd} setRefetch={refetch}  /> */}
            <QrCodeShow open={openQRCode} setOpen={setOpenQRCode} data={qrCodeData} />
            <AddShop open={openAdd} setOpen={setOpenAdd} setRefetch={refetch} personalInfo={personalInfo} />

            <Col
                xs={24}
                md={4}
                style={{padding:3}}
            >
                <Button
                    type="primary"
                    onClick={() => setOpenAdd(true)}
                >
                    
                    <PlusOutlined />
                </Button>
            </Col>
            
            <Col 
                xs={24}
                md={{span:4}}
                style={{padding:3}}
                >
              
                <RangePicker 
                    // onChange={(e) => setFilterDate(e)}  
                    defaultValue={[location?.state?.date===null || location?.state?.date===undefined ? null :moment(location?.state?.date),location?.state?.date===null || location?.state?.date===undefined ? null :moment(location?.state?.date)]}
                    onChange={e => onSelectDate(e)} 
                    placeholder={dateHolder} 
                    style={{width:"100%"}} 
                />
            </Col>
        
            <Col
                xs={24}
                md={{span:4}}
                style={{padding:3}}
            >
                <Input.Search
                    onChange={(e)=>setKeyword(e.target.value)}
                    placeholder="..."
                />
            </Col>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Table
                    className="table-go-list"
                    columns={shopCol({handleDelete,limit,page,handleGenerateQR,handleOpenAddShop})}
                    dataSource={shopDataWithNo}
                    rowKey={record => record.id}
                    // pagination={{
                    //     total: shopData?.paginator?.totalDocs,
                    //     //pageSizeOptions:["10", "20"],
                    //     // showSizeChanger: true,
                    //     pageSize:limit,
                    //     // current: shopData?.paginator?.currentPage,
                    //     onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                    // }}
                    scroll={{ x: 1000 }} 
                    sticky 
                    loading={loading}
                    
                />
            </Col>
        </Row>
    )
}
