import { Col, Row, Typography, Table, message, Input, DatePicker, List, Button, Popconfirm,Card,Modal, Space, Select } from 'antd'
import { ListGroup } from 'react-bootstrap'
import React, { useState, useEffect,useRef } from 'react'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import {
    ReconciliationOutlined,
    EditOutlined, 
   
} from '@ant-design/icons';
import { useMutation, useQuery } from '@apollo/client'

import { GET_PEOPLE_BY_SHOP, GET_SHOP_BY_ID } from '../../graphql/shop'
import { subShopCol } from './tableColumn/subShopColumn'
import EditShop from './modal/editShop'

const { RangePicker } = DatePicker
const { Title } = Typography
const {Option} = Select

export default function SubShop() {

    let { id } = useParams();
    
    const [subShopData, setSubShopData] = useState([])
    const [shopData, setShopData] = useState({})

    const [openEdit,setOpenEdit] = useState(false)

    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    const [marketName,setMarketName] = useState("")
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })   

    const { data, refetch } = useQuery(GET_SHOP_BY_ID, {
        variables: {
            shopId: id
        },
        onCompleted: ({ getShopById }) => {
            setShopData(getShopById)
        }
    })
    useEffect(() => {
        if (data) {
            setShopData(data?.getShopById)
        }
    }, [data])

    const {data:shopSub,loading:loadingSub,refetch:refetchSub} = useQuery(GET_PEOPLE_BY_SHOP,{
        variables:{
            page:page,
            limit:limit,
            keyword:keyword,
            marketName:marketName,
            startDate:ranges?.startDate,
            endDate:ranges?.endDate,
            shopId:id
        },
        onCompleted:({getAffectedPeopleByShopIdWithPagination})=>{
            // console.log(getAffectedPeopleByShopIdWithPagination)
            setSubShopData(getAffectedPeopleByShopIdWithPagination)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only",
    })

    useEffect(() => {
        refetch()
    }, [page,limit,keyword,ranges,marketName])

    useEffect(()=>{
        if(shopSub!==undefined){
            setSubShopData(shopSub?.getAffectedPeopleByShopIdWithPagination)
        }
    },[shopSub])

    const dateHolder = ["", ""]

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

   
    // get no for people data
    let personalDataWithNo = []
    subShopData?.transactions?.map((record,index) =>{

        let pageAdd = subShopData?.paginator?.currentPage > 1 ? ((subShopData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}
        // console.log(record)
        if(record?.personalInfoId?.lastName===undefined){ 
            setNoToPeopleData = {...setNoToPeopleData,personalInfoId:{...record?.personalInfoId,firstName:"",lastName:"",id:null}}
        }
            
        personalDataWithNo.push(setNoToPeopleData)

    })

    return (
        <>
            
            <Row>
                <EditShop open={openEdit} setOpen={setOpenEdit} data={shopData} shopId={id} setRefetch={refetch} />
                <Col
                    xs={24}
                    md={24}
                >
                    
                </Col>
                <Col
                            xs={12}
                            md={{span: 11, offset: 0}}
                    // className={rpsPadding}
                >
                    <Card 
                        // className="subHospital-card" 
                        className="tbl-subPeople" 
                        title={
                            <span style={{justifyContent:"space-between", display:"flex"}}>
                                <span>
                                    <span> <ReconciliationOutlined style={{fontSize:25,paddingRight: 10}}  /></span>
                                    <span className="title-card-custom " style={{fontSize:20}}></span>
                                </span>
                                <Button
                                    type="primary"   
                                    onClick={() => setOpenEdit(true)}
                                >
                                    <EditOutlined style={{color:"#FFF"}} />
                                </Button>
                            </span>
                        } 
                        style={{height:'100%'}}
                    >
                        {/* <span style={{fontSize:40}}>200</span>  */}
                        
                        <Row>
                            <Col xs={24}
                                md={{span: 11, offset: 0}}
                                >
                                
                                <table className="tbl-subPeople">
                                    <tbody>
                                    <tr>
                                        <td></td>
                                        <td></td>
                                        <td>{shopData?.name}</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp;</td>
                                        <td>
                                            {shopData?.shopNumber}
                                        </td>
                                        
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp; </td>
                                        <td>
                                            {shopData?.village !== "" && shopData?.village + ","}
                                            {shopData?.commune !== "" && shopData?.commune + ","}
                                            {shopData?.district !== "" && shopData?.district + ","}
                                            {shopData?.province}
                                        </td>
                                        
                                    </tr>

                                    <tr>
                                        <td></td>
                                        <td>&emsp;</td>
        
                                        <td>{shopData?.personalInfoId?.lastName} {shopData?.personalInfoId?.firstName}</td>
                                        {/* <td>&emsp;</td>
                                        <td>&emsp;</td>
                                        <td>&emsp;</td> */}
                                    </tr>
                                    
                                   
                                    </tbody>
                                </table>
                            </Col>
                            
                            <Col  xs={24}
                                    md={{span: 11, offset: 2}}>
                               
                                <table className="tbl-subPeople">
                                    <tbody>
                                    <tr>
                                        <td></td>
                                        <td>&emsp;</td>
                                        <td>
                                            {shopData?.tel}
                                        </td>
                                        
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp;</td>
                                        <td>
                                            {shopData?.marketName}
                                        </td>
                                        
                                    </tr>
                                    <tr>
                                        <td>Registration Number</td>
                                        <td>&emsp; </td>
                                        <td>{shopData?.registrationNumber}</td>
                                    </tr>
                                    <tr>
                                        <td>Acknowledge As</td>
                                        <td>&emsp; </td>
                                        <td>{shopData?.acknowledgeAs}</td>
                                    </tr>
                                </tbody>
                                </table>
                            </Col>
                        </Row>

                    </Card>
                    
                
                </Col>
                <Col
                    xs={24}
                    md={{span:12}}
                    // className="subCase-card"
                >
                    {/* <Row style={{height:'70%'}}>
                        <Col xs={24}
                                md={8}>
                            <Card
                            className="tbl-subPeople" 
                             title={
                            <span> 
                                <span> <UsergroupAddOutlined style={{ fontSize: '23px',color:"red",paddingRight: 10}} /></span>
                                <span className="title-card-custom"></span>
                            </span>
                             
                             } style={{height:'100%'}}>
                                 <center>
                                        <table className="tbl-subPeople">
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalIn} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalInWomen} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalInToday} </td>
                                                </tr>
                                                <tr>
                                                    <td><b>DELTA</b></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalDeltaIn} </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </center>
                            </Card>
                        </Col>
                        <Col xs={24}
                                md={8}> 
                            <Card className="tbl-subPeople"  
                            title={
                            <span>
                                <span> <UsergroupDeleteOutlined style={{ fontSize: '23px',color:"green",paddingRight: 10}} /></span>
                                <span className="title-card-custom"></span>
                            </span>
                            } style={{height:'100%'}}>
                                  <center>
                                        <table className="tbl-subPeople">
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalOut} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalOutWomen} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalOutToday} </td>
                                                </tr>
                                                <tr>
                                                    <td><b>DELTA</b></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalOutDelta} </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </center>
                            </Card>
                        </Col>
                        <Col xs={24}
                                md={8} >
                            <Card className="tbl-subPeople"  title={
                            <span>
                                <span><UserOutlined style={{ fontSize: '23px',color:"red",paddingRight: 10}} /></span>
                                <span className="title-card-custom"></span>
                            </span>
                            } style={{height:'100%'}}>
                                  <center>
                                        <table className="tbl-subPeople">
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalIn - dashboard?.totalOut} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{parseInt(dashboard?.totalInWomen - dashboard?.totalOutWomen)}</td>
                                                </tr>
                                                <tr>
                                                    <td><b>DELTA</b></td>
                                                    <td>&emsp;</td>
                                                    <td>{parseInt(dashboard?.totalDeltaIn - dashboard?.totalOutDelta)} </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </center>
                            </Card>   
                        </Col> 
                        
                    </Row>*/}

                </Col>

                
                {/* <Col
                xs={24}
                md={{ span: 11, offset: 2 }}
                className="subCase-card"
            >
                <p> </p>

                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
            </Col> */}


                {/* <Col
                    xs={24}
                    style={{ marginTop: 20 }}
                >
                    <Title level={5}>
                         {" "}

                        <PlusCircleOutlined className="link" onClick={() => setOpenAddSub(true)} />
                    </Title>

                </Col> */}
                <Col
                xs={24}
                md={24}
                style={{ margin: "30px 0 30px 0" }}
            >
                    <Row>
                        <Col
                            xs={24}
                            md={24}
                        >
                            <Row>

                                <Col 
                                    xs={24}
                                    md={{span:6}}
                                    style={{padding:3}}
                                    >
                                
                                    <RangePicker 
                                        // onChange={(e) => setFilterDate(e)}  
                                        onChange={e => onSelectDate(e)} 
                                        placeholder={dateHolder} 
                                        style={{width:"100%"}} 
                                    />
                                </Col>
                                    
                                <Col
                                    xs={24}
                                    md={6}
                                    style={{padding:3}}
                                >
                                
                                <Input.Search style={{width:'100%'}} onChange={(e)=>setKeyword(e.target.value)} placeholder=""/>
                                
                                </Col>
                            </Row>
                        </Col>
                        
                    </Row>
                </Col>
                <Col
                    xs={24}
                    md={24}
                >
                    <Table
                        columns={subShopCol()}
                        dataSource={personalDataWithNo}
                        rowKey={record => record.id}
                        pagination={{
                            total: subShopData?.paginator?.totalDocs,
                            // showSizeChanger: true,
                            pageSize:limit,
                            onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                        }}
                        scroll={{ x: 900 }}
                        sticky
                        loading={loadingSub}
                        
                    />
                </Col>

            </Row>
        </>
    )
}
