import {FOR_BAR_GRAP} from '../../graphql/dashboardAndReport'
import { useQuery } from "@apollo/client";
import React, { useState, useEffect } from "react";
import { Bar,Line } from "react-chartjs-2";
import numeral from "numeral";


function BarGraph() {
  const [data, setData] = useState({});

  const {loading,refetch} = useQuery(FOR_BAR_GRAP,{onCompleted:({getDataForBarGraphTotal})=>{
    setData(getDataForBarGraphTotal);
  }})
let color = "rgb(167, 11, 153)";

const a = [data?.confirm,data?.recovered,data?.deaths,data?.confirm - data?.recovered];
const b =["","","",""];


const datas = {
  labels: b,
  datasets: [
    {
      label: "",  
      data: a,
      fill: false,
      backgroundColor:["#ffd666","#73d13d","#cf1322","yellow"],
      borderColor: color
    },
  ]
};


  return (
    <div>
        <Bar
        data={datas}
      //   options= {
      //     {
      //     scales: {
      //         yAxes: [{
      //             ticks: {
      //                 beginAtZero: true
      //             }
      //         }]
      //     }
      //   }
      // }
      options={{
        maintainAspectRatio: true,
        
        plugins: {
          legend: {
              labels: {
                  // This more specific font property overrides the global property
                  font: {
                      size: 15,
                      family:"Khmer OS Siemreap"
                  },
                  // color:'#e6fffb'

              },
              
             
          },
          datalabels:{
            // display:false,
            // color:'#FFFFFF',
            anchor:'end',
            align:'top',
            font:{
              size:10
            }
          },
          //for chart title future use
          // title:{
          //   display:true,
          //   text:"test",
          //   font:{
          //     size:40
          //   }
          // },
          tooltip: {
            // enabled:false,
            // title:{
            //   display:true,
            //   fontSize:50
              // font: {
              //   size:50,
              //   family:"Khmer OS Siemreap"
              // }
            // }

            
         
          }
        },
        scales: {
          x: {
              ticks: {
                  font: {
                      size: 13,
                      family:"Khmer OS Siemreap"
                  },
                
              }
          },
          y: {
            ticks: {
                font: {
                    size: 13,
                    family:"Khmer OS Siemreap",
                    
                },
                // mirror:true,
                
            }
          }
        },

      }}
        />
    </div>
  );
}

export default BarGraph;