import {gql} from '@apollo/client'

export const ADD_SELLER = gql`
mutation createPersonalInfo(
    $idCard:String,
    $firstName:String,
    $lastName:String,
    $englishName:String,
    $dob:DateTime,
    $gender:String,
    $age:Int,
    $occupation:String,
    $tel:String,
    $nationality:String,
    $village:String,
    $commune:String,
    $district:String,
    $province:String,
    $seller:Boolean
){
    createPersonalInfo(newInfo:{
        idCard:$idCard
        firstName:$firstName
        lastName:$lastName
        englishName:$englishName
        dob:$dob
        gender:$gender
        age:$age
        occupation:$occupation
        tel:$tel
        nationality:$nationality
        village:$village
        commune:$commune
        district:$district
        province:$province
        seller:$seller
    }){
        response{
            success
            message
        }
        personalInfo{
            id
            firstName
            lastName
        }
    }
      
}
`
export const GET_SELLER_WITH_PAGINATION = gql`
query ($keyword:String,$page:Int,$limit:Int,$isSeller:Boolean){
    getSellerWithpagination(keyword:$keyword,page:$page,limit:$limit,isSeller:$isSeller){
        personalInfos{
            id
            idCard
            firstName
            lastName
            gender
            age
            village
            commune
            district
            province
            occupation
            tel
            nationality
          }
          paginator{
              slNo
              prev
              next
              perPage
              totalPosts
              totalPages
              currentPage
              hasPrevPage
              hasNextPage
              totalDocs
            }
    }
  }
`

