export const CREATE_SHOP = gql`
mutation createShop(
    $name: String,
    $registrationNumber: String,
    $acknowledgeAs: String,
    $registerDate: DateTime,
    $village: String,
    $commune: String,
    $district: String,
    $province: String,
    $firstName: String,
    $lastName: String,
    $personalInfoId: ID,
    $shopNumber:String,
    $tel:String,
    $marketName:String
){
    createShop(newShop:{
      name: $name
      registrationNumber: $registrationNumber
      acknowledgeAs: $acknowledgeAs
      registerDate: $registerDate
      village: $village
      commune: $commune
      district: $district
      province: $province
      firstName: $firstName
      lastName: $lastName
      personalInfoId: $personalInfoId
      shopNumber:$shopNumber
      tel:$tel,
      marketName:$marketName
    }){
      message
      success
    }
  }
`