import { Space, Popconfirm,Tooltip } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined,
    QrcodeOutlined
} from '@ant-design/icons';
import { AiOutlineShop } from 'react-icons/ai'
import {GiShop} from 'react-icons/gi'
import { Link } from 'react-router-dom';
import moment from 'moment';
import { getKhmerTime } from '../../../function/fn';

export const buyerCol = () => {

    var array = [
        {
            title: '.',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            
        },
        {
            title: '',
            dataIndex: 'shopNumber',
            key: 'shopNumber',
            width: 100,
            render: (text, record, i) => (
                    <span>
                        {record?.shopId?.shopNumber}
                    </span>
            ),
        },
        
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 100,
            render: (text, record, i) => (
                <span>
                    {record?.shopId?.name}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'seller',
            key: 'seller',
            width: 130,
            render: (text, record) => (
                <span>
                    {record?.shopId?.lastName} {record?.shopId?.firstName}
                    {console.log(record)}
                </span>
            ),
        },
        
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 200,
            render: (text, record) => (
                <span>
                    {(record?.shopId?.village !== "") && record?.shopId?.village+", "}
                    {record?.shopId?.commune !== "" && record?.shopId?.commune+", "}
                    {record?.shopId?.district !== "" && record?.shopId?.district+", "}
                    {record?.shopId?.province && record?.shopId?.province}
                </span>
            ),
        },
    
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 120,
            render: (text, record, i) => (
                <span>
                    {record?.shopId?.tel}
                </span>
            ),
        },

        {
            title: '',
            dataIndex: 'createdAt',
            key: 'createdAt',
            width: 170,
            render: (text, record, i) => (
                <span>
                    {moment(record?.createdAt).format("DD MM YYYY") }  {getKhmerTime(record?.createdAt)}
                </span>
            ),
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 100,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subShop/" + record?.shopId?._id}><EyeOutlined /></Link>
                    {/* <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record._id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                    <Tooltip
                        title=" QR Code"
                    >
                        <span className="link" ><QrcodeOutlined onClick={()=> handleGenerateQR(record)} /></span>
                    </Tooltip> */}

                    {/* <Tooltip
                        title=""
                    >
                        
                        <span className="link" ><AiOutlineShop style={{fontSize:18}} onClick={()=> handleOpenAddShop(record)} /></span>
                    </Tooltip> */}

                </Space>
            ),
        }
    ]
    return array
}